@import "normalizez.scss";
@import "resets.scss";

$main-color: #990338;
$second-color-2: #FFFFFF;
$third-color-3: #000000;
$background-color: #010C1D;
.main-section {
    overflow: hidden; }
.container {
    max-width: 1420px; }

.containers {
    max-width: 914px; }

.containerz {
    max-width: 592px; }

.containersz {
    max-width: 1000px; }

.containerzs {
    max-width: 976px; }

.containerx {
    max-width: 622px; }

.main-section {
    &__background-image {}
    width: 100%;
    background: url("../img/ben-neale-zpxKdH_xNSI-unsplash.jpg");
    min-height: 800px;
    margin: 0 auto;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    overflow: hidden;
    z-index: 0;
    &__linear-gradient {
        position: absolute;
        margin: 0 auto;
        width: 100%;
        min-height: 391px;
        top: 567px;
        background: linear-gradient(180deg, rgba(0, 12, 28, 0) 0%, #000C1C 41.15%);
        z-index: 2; } }

.nav-menu {
    z-index: 999;
    margin-top: 46px;
    font-size: 20px;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    text-shadow: 0px 0px 10px $main-color;
    padding-bottom: 300px;
    &__list {
        display: flex;
        align-items: center;
        justify-content: space-around; }
    a {
        color: $second-color-2;
        text-decoration: none; }
    &__list-item a:hover {
        color: $main-color;
        text-shadow: 0px 0px 10px $second-color-2; } }

.main-section__amodotomi {
    margin: 0 auto;
    position: absolute;
    width: 100%;
    z-index: 998;
    top: 300px;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
    font-size: 64px;
    letter-spacing: 0.35em;
    color: $second-color-2;
    text-shadow: 0px 0px 10px $second-color-2;
    h1 {
        text-align: center; } }

//  Second Section

.second-section__background {
    background-color: $background-color;
    display: relative; }

.second-section__services {
    margin: 0 auto;
    position: absolute;
    left: calc(50% - 299px/2 + 0.5px);
    width: 299px;
    height: 55px;
    text-align: center;
    top: 904px;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 700;
    font-size: 48px;
    line-height: 115.02%;
    letter-spacing: 0.15em;
    color: $second-color-2;
    text-shadow: 0px 0px 10px $main-color;
    padding-top: 25px;
    margin-bottom: 62px;
    z-index: 3; }

.box {
    width: 180px;
    height: 180px;
    background: $main-color;
    box-shadow: 0px 0px 20px $main-color;
    border-radius: 25px; }

.second-section__boxes {
    display: flex;
    align-content: center;
    justify-content: space-between;
    align-items: center;
    margin: auto;
    padding-top: 260px;
    margin-bottom: 33px; }


.second-section__boxes-item {
    display: flex;
    justify-content: center;
    align-items: center; }

.second-section__boxez {
    display: flex;
    align-content: center;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto; }



.second-section__boxez-item {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 30px; }

.second-section__texts {
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 30px;
    color: $main-color;
    margin-bottom: 48px;
    p {
        width: 180px;
        display: flex;
        justify-content: center; } }

.second-section__textz {
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 30px;
    color: $main-color;
    p {
        width: 180px;
        display: flex;
        justify-content: center;
        margin-bottom: 70px; }

 }    // THIRD SECTION

.second-section__line {
    max-width: 940px;
    height: 0px;
    margin: 0 auto;
    border-top: 1px solid $main-color;
    box-shadow: 0px 0px 10px $main-color;
